  padding-left: 2rem;
}
.txt2Div > p {
  margin: 0;
  text-transform: uppercase;
  font-size: 4.5rem;
  line-height: 4rem;
  color: #dedede;
}
.right {
  margin-bottom: 20rem;
  margin-top: 16rem;
  text-align: right;
  font-weight: 500;
  color: #dedede;
  padding: 2rem;
}
.right > p {
  position: relative;
  display: inline-block;
  font-size: 1.5rem;
  font-weight: 500;
}
.right > p > a {
  position: absolute;
  bottom: -145%;
  right: -8%;
  text-align: left;
  transform: rotate(90deg);
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #dedede;
  opacity: 0.3;
  font-size: 6rem;
  line-height:6rem;

}
@media only screen and (max-width: 960px) {
  .txtDiv {
